import { Component } from '@angular/core';
import { MultiLangService } from '../shared/multi.service';

@Component({
    selector:'pricing',
    templateUrl:'./pricing.component.html'
})

export class PricingComponent {

    lang:string;
    pricingContent:any;

    constructor(private multi:MultiLangService){}

    ngOnInit(){
        this.lang = this.multi.lang;
        this.content(this.lang)
        this.multi.tlang.subscribe(
            (lang:string) => {
                this.lang = this.multi.lang
                this.content(lang)
            }
        )
    }

    content(lang:string){
        if(lang === 'eng'){
            this.pricingContent = this.multi.pricing.eng;
        }
        else if(lang === 'som'){
            this.pricingContent = this.multi.pricing.som;
        }
        else if(lang === 'arb'){
            this.pricingContent = this.multi.pricing.arb;
        }
    }
}